import React from 'react';

import Layout from '../components/Layout';

// import { Link } from 'gatsby';
import Sidebar from '../components/Sidebar';
import config from '../../config';
const IndexPage = () => (
  <Layout>
    <Sidebar />
    <div className="container-fluid p-0">
      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="about"
      >
        <div className="w-100">
          <h1 className="mb-0">
            {config.firstName}
            <span className="text-primary">{config.lastName}</span>
          </h1>
          <div className="subheading mb-5">
            {config.address} · {config.phone} ·
            <a href={`mailto:${config.email}`}>{config.email}</a>
          </div>
          <p className="lead mb-5">
            A polyglot developer with around a decade worth of experience who is capable of
            designing and developing enterprise application. An agile enthusiast with creativity
            and analytical skils bounded by passion of craftmanship.
          </p>
          <div className="social-icons">
            {config.socialLinks.map(social => {
              const { icon, url } = social;
              return (
                <a key={url} href={url}>
                  <i className={`fab ${icon}`}></i>
                </a>
              );
            })}
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex justify-content-center"
        id="experience"
      >
        <div className="w-100">
          <h2 className="mb-5">Experience</h2>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">Lead Developer</h3>
              <div className="subheading mb-3">Terrabit Consulting, MY</div>
              <p>
                Deployed to Standard Chartered Bank, Malaysia.
                Doing Research and Development with the latest trend and technologies.
                Design and developing performant microservices using the 12 factor methodology.
              </p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">March 2019 - Present</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">Lead Developer</h3>
              <div className="subheading mb-3">IBM Solutions Delivery, PH</div>
              <p>
                Deployed to Bank of the Philippine Island, Philippines.
                Doing Research and Development with the latest trend and technologies.
                Design and developing performant microservices using the 12 factor methodology.
              </p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">October 2018 - March 2019</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">Lead Developer</h3>
              <div className="subheading mb-3">IBM Solutions Delivery, PH</div>
              <p>
                Deployed to Siam Commercial Bank, Thailand.
                Doing Research and Development with the latest trend and technologies.
                Design and developing performant microservices using the 12 factor methodology.
              </p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">March 2017 - October 2018</span>
            </div>
          </div>

        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="education"
      >
        <div className="w-100">
          <h2 className="mb-5">Education</h2>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">University of Negros Occidental-Recoletos</h3>
              <div className="subheading mb-3">Master of Science in Computer Science</div>
              <div>Computer Science - Software Engineering</div>
              <p></p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">March 2002</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between">
            <div className="resume-content">
              <h3 className="mb-0">University of Negros Occidental-Recoletos</h3>
              <div className="subheading mb-3">Bachelor of Science in Computer Science</div>
              <p></p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">March 1998</span>
            </div>
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="skills"
      >
        <div className="w-100">
          <h2 className="mb-5">Skills</h2>

          <div className="subheading mb-3">
            Programming Languages &amp; Tools
          </div>
          <ul className="list-inline dev-icons">
          <li className="list-inline-item">
            <i class="devicon-java-plain"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-go-plain"></i>
            </li>
            <li className="list-inline-item">
              <i class="devicon-amazonwebservices-original"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-apache-plain"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-mongodb-plain"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-git-plain"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-tomcat-line"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-docker-plain"></i>
            </li>
            <li className="list-inline-item">
            <i class="devicon-cucumber-plain"></i>
            </li>
          </ul>

          <div className="subheading mb-3">Workflow</div>
          <ul className="fa-ul mb-0">
            <li>
              <i className="fa-li fa fa-check"></i>
              Domain Driven Design
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Microservice Development
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Agile Development &amp; Scrum
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              DEVOPS (Docker, Kubernetes)
            </li>
          </ul>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="interests"
      >
        <div className="w-100">
          <h2 className="mb-5">Interests</h2>
          <p>
            Apart from being an enterprise designer and developer, I enjoy travelling. 
            I beach lover and enjoy food adventure.
          </p>
          <p className="mb-0">
            When forced indoors, I like to watch food show and suspense movies.
            I spend a large amount of my free time exploring the latest technology
            in backend and microservice development.
          </p>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="awards"
      >
        <div className="w-100">
          <h2 className="mb-5">Awards &amp; Certifications</h2>
          <ul className="fa-ul mb-0">
            <li>
              <i className="fa-li fa fa-trophy text-warning"></i>
              Oracle Certified Java Programmer
            </li>
            <li>
              <i className="fa-li fa fa-trophy text-warning"></i>
              Oracle Certified Web Component Developer
            </li>
            <li>
              <i className="fa-li fa fa-trophy text-warning"></i>
              Oracle Certified Business Component Developer
            </li>
            <li>
              <i className="fa-li fa fa-trophy text-warning"></i>
              Certified Professional Ethical Hacker
            </li>
            <li>
              <i className="fa-li fa fa-trophy text-warning"></i>
              Certifier Scrum Master
            </li>
          </ul>
        </div>
      </section>
    </div>
  </Layout>
);

export default IndexPage;
